import React from "react";

const ProgressBar = ({ title, ratio }) => {
  return (
    <div className="mt-3">
      <h6 className="text-left text-sm mb-1 dark:text-gray-300">{title}</h6>
      <div className="h-2 relative max-w-xl rounded-full overflow-hidden ">
        <div className="w-full h-full bg-gray-200 absolute "></div>
        <div
          className="h-full bg-gradient-to-r from-pink-400 bg-green-500 absolute z-10"
          style={{ width: `${ratio}%` }}
        ></div>
      </div>
    </div>
  );
};

export default ProgressBar;
