import React from "react";
import SkillCard from "./SkillCard";
import { skills, tools } from "../data/data";
import { motion } from "framer-motion";

const Skills = () => {
  return (
    <div className="py-24 md:py-40" id="skills">
      <h1 className="text-green-400 text-5xl font-semibold">Skills</h1>
      <div className="flex justify-center mb-5 mt-5 gap-5 flex-wrap md:flex-nowrap">
        <div className="w-full md:w-1/2">
          <SkillCard
            title="  Programming Languages & Markup"
            skills={skills.languages}
          />
        </div>
        <div className=" w-full md:w-1/2">
          <SkillCard
            title="  Libraries & Framworks"
            skills={skills.framworks}
          />
        </div>
      </div>

      <motion.div className="flex flex-col  mt-4 gap-4" whileHover={{ y: 10 }}>
        <div className="border dark:border-0 dark:bg-gray-800 w-full  rounded-sm shadow-lg px-3 py-5">
          <h1 className="text-lg  dark:text-gray-200 ml-4">Other</h1>

          {tools &&
            tools.map((tool) => (
              <div className="px-3 mt-3  dark:text-gray-100" key={tool.label}>
                <span className="text-green-500">{tool.label}</span>{" "}
                {tool.values.join(", ")}
              </div>
            ))}
        </div>
      </motion.div>
    </div>
  );
};

export default Skills;
