import React from "react";
import DefaultLayout from "./layouts/DefaultLayout";
import ArchiveTable from "./ArchiveTable";
import { Link } from "react-router-dom";

import { projects as projects_data } from "../data/data";

const Archive = () => {
  return (
    <DefaultLayout>
      <h1 className="text-gray-700 dark:text-gray-200 text-7xl pt-32">
        Archive
      </h1>
      <h2 className="text-primary-dark-theme">a List of Projects I've build</h2>
      <Link to="/">
        <button className="text-center text-green-400 underline mt-5">
          Back to Home
        </button>
      </Link>
      <ArchiveTable projects={projects_data} />
    </DefaultLayout>
  );
};

export default Archive;
