import React, { useState, useEffect, useCallback } from "react";
import ThemeSwitch from "./TemeSwitch";
import BaseNavLink from "./BaseNavLink";
import { scroller } from "react-scroll";
import { withRouter } from "react-router-dom";
import HamburgerMenu from "./HamburgerMenu";

const Nav = (props) => {
  const [pos, setPos] = useState(0);
  const [windowWidth, setWindowWidth] = useState();
  const [side, setSide] = useState(false);

  useEffect(() => {
    if (windowWidth > 768) {
      setSide(false);
    }
  }, [windowWidth]);

  const handleScroll = useCallback((e) => {
    const newPos = window.pageYOffset;
    setPos(newPos);
  }, []);

  const handleResize = useCallback((e) => {
    setWindowWidth(window.innerWidth);
  }, []);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener("resize", handleResize);
    };
  }, [handleResize, handleScroll]);

  useEffect(() => {
    if (props.history.location.hash) {
      const to = props.history.location.hash.split("#")[1];

      scroller.scrollTo(to, {
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuart",
      });
    }
  }, [props.history.location.hash]);

  //scrollSpy
  const NavLinks = useCallback(
    () => (
      <>
        <BaseNavLink to="about_me">AboutMe</BaseNavLink>
        <BaseNavLink to="skills">Skills</BaseNavLink>
        <BaseNavLink to="projects">Projects</BaseNavLink>
        <BaseNavLink to="get_in_touch">Get In Touch</BaseNavLink>
      </>
    ),
    []
  );

  return (
    <div
      className={`w-full h-16 bg-gray-100 dark:bg-gray-900 z-50 nav-slide-in fixed shadow-lg
   
      ${pos === 0 ? "shadow-none" : ""}`}
    >
      <div
        className={`h-screen w-full  absolute right-0 ${
          side ? "block" : "hidden"
        }`}
      >
        <div
          className="h-full w-8/12 absolute right-0 bg-gradient-to-b from-gray-200 to-gray-100 dark:from-gray-700 dark:to-gray-800"
          style={{ filter: "blur(0)" }}
        >
          <div className="flex flex-col items-center  gap-10 mt-16">
            <NavLinks />
            <ThemeSwitch />
          </div>
        </div>
      </div>

      <div className="flex justify-end items-center md:hidden h-full mr-5">
        <HamburgerMenu opened={side} toggle={() => setSide(!side)} />
      </div>
      <div className="hidden md:flex justify-between items-center h-full mr-14 gap-10 transition delay-1000 font-mono  text-sm">
        <div className="flex items-center h-full ml-5">
          <ThemeSwitch />
        </div>

        <div className="md:w-8/12 flex justify-end gap-6">
          <NavLinks />
        </div>
      </div>
    </div>
  );
};

export default withRouter(Nav);
