import React from "react";
import PropTypes from "prop-types";
import ProgressBar from "./ProgressBar";
import { motion } from "framer-motion";

const SkillCard = ({ title, skills }) => {
  return (
    <motion.div
      className="rounded-sm shadow-lg border dark:border-0 dark:bg-gray-800 h-full p-5"
      whileHover={{ y: -10 }}
    >
      <h2 className="text-center mt-4 dark:text-gray-300 text-lg">{title}</h2>

      <div className="px-6 pb-8 mt-5">
        {skills &&
          skills.map((skill, i) => (
            <ProgressBar title={skill.title} ratio={skill.ratio} key={i} />
          ))}
      </div>
    </motion.div>
  );
};

SkillCard.propTypes = {
  title: PropTypes.string.isRequired,
  skills: PropTypes.array.isRequired,
};

export default SkillCard;
